import {
  Cog6ToothIcon,
  Cog8ToothIcon,
  ExclamationCircleIcon,
  PhotoIcon,
  PlusIcon,
  SparklesIcon,
  XMarkIcon,
} from "@heroicons/react/24/outline";
import { Checkbox, Radio, Slider, Switch, Tooltip } from "antd";
import TextArea from "antd/lib/input/TextArea";
import { Script } from "gatsby";
import * as React from "react";
import { LaunchButton, SecondaryButton } from "../../atoms";
import {
  IStatus,
  IGeneratorConfig,
  IGeneratorResult,
  IUIConfig,
} from "../../types";
import {
  getLocalStorage,
  loadJSONData,
  rebalanceWeights,
  setLocalStorage,
  truncateText,
} from "../../utils";
import LatentFlow from "../editor/latentflow";
import GeneratorControlsView from "./controls";
import GalleryView from "./gallery";
import Prompts from "./prompts";
import SamplesView from "./samples";
import UploadView from "./upload";

var JSZip = require("jszip");

const GenerateView = () => {
  // const reconnectInterval = 1000;
  let initConfig = getLocalStorage("config");
  let initUIConfig: IUIConfig = getLocalStorage("uiConfig") || {
    alignWeights: true,
  };
  const errorMsg: IStatus = {
    status: false,
    message:
      "Error fetching generation. There is a chance the server is down. Visit the peacasso repo to learn more about running your own server",
  };

  if (initConfig === null || !Array.isArray(initConfig.prompt)) {
    initConfig = {
      prompt: [
        "portrait of leprechaun, intricate, elegant, highly detailed, digital painting, artstation, concept art, smooth, sharp focus, illustration, art by artgerm and greg rutkowski and alphonse mucha, 8 k ",
      ],
      num_images: 6,
      guidance_scale: 7.5,
      num_inference_steps: 20,
      height: 512,
      width: 512,
      prompt_weights: [1],
      strength: 0.8,
    };
  }
  const currentProtocol =
    (typeof window !== "undefined" && window.location.protocol) || "http:";
  // const wsProtocol = curretProtocol === "https:" ? "wss://" : "ws://";

  const serverUrl = process.env.GASTSBY_API_SERVER + "/api";
  const [loading, setLoading] = React.useState(false);
  const [results, setResults] = React.useState<null | IGeneratorResult>(null);
  const [config, setConfig] = React.useState<IGeneratorConfig>(initConfig);
  const [error, setError] = React.useState<null | IStatus>(null);
  const [advancedOptions, setAdvancedOptions] = React.useState(false);
  const [selectedPrompt, setSelectedPrompt] = React.useState(0);
  const [uiConfig, setUiConfig] = React.useState<IUIConfig>(initUIConfig);

  const updateInitImage = (image: any) => {
    // console.log("setting init image to ", image ? image.slice(0, 100) : image);

    setConfig({
      ...config,
      init_image: image,
      mask_image: null,
    });
    // setUploadedImage(image);
  };

  React.useEffect(() => {
    // console.log("config changed", config);
    setLocalStorage("config", { ...config, init_image: null });
  }, [config]);

  React.useEffect(() => {
    // console.log("uiConfig changed", uiConfig);
    setLocalStorage("uiConfig", uiConfig);
  }, [uiConfig]);

  React.useEffect(() => {
    promptInputRef.current?.focus();
    promptInputRef.current!.value = config.prompt[selectedPrompt];
  }, [selectedPrompt]);

  const resetError = () => {
    setError(null);
  };

  // fetch generation
  const fetchGeneration = (config: IGeneratorConfig) => {
    setResults(null);
    setError(null);
    setLoading(true);
    const generateUrl = `${serverUrl}/generate`;

    const postData = {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ type: "generate", config: config }),
    };
    fetch(generateUrl, postData)
      .then((res) => {
        if (res.status === 200) {
          return res.json();
        } else {
          setError({
            status: false,
            message: `Error ${res.status} c: ${res.statusText}`,
          });
          setLoading(false);
        }
      })
      .then((data) => {
        if (data) {
          if (data.status) {
            setResults(data.result);
            setLoading(false);
          } else {
            console.log(data.status_message);
            setError({
              status: false,
              message: data.status_message,
            });
            setLoading(false);
          }
        }
      })
      .catch((err) => {
        setError(errorMsg);
        setLoading(false);
      });
  };

  function getBase64(file: any) {
    var reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = function () {
      updateInitImage(reader.result);
    };
    reader.onerror = function (error) {
      setError({ status: false, message: error + "" });
    };
  }

  const promptList = config.prompt.map((prompt: string, i: number) => {
    const isSelected = i === selectedPrompt;
    return (
      <div key={"propmtitem" + i} className="  flex flex-col">
        <div>
          <div
            className={` inline-block relative   rounded ${
              isSelected ? "bg-accent text-white" : "bg-secondary"
            }`}
          >
            <div
              role={"button"}
              onClick={() => {
                setSelectedPrompt(i);
              }}
              className={`inline-block p-2  px-3 `}
            >
              {config.use_prompt_weights && (
                <span
                  className="font-semibold inline-block   px-1 "
                  style={{ minWidth: "40px" }}
                >
                  {config.prompt_weights && config.prompt_weights[i].toFixed(2)}
                </span>
              )}{" "}
              {truncateText(prompt, 30) || ".."}{" "}
            </div>
            <div
              role={"button"}
              onClick={() => {
                if (config.prompt.length > 1) {
                  if (config.prompt_weights) {
                    setConfig({
                      ...config,
                      prompt: config.prompt.filter((p, j) => j !== i),
                      prompt_weights: config.prompt_weights.filter(
                        (p, j) => j !== i
                      ),
                    });
                  }
                  setSelectedPrompt(0);
                }
              }}
              className="text-secondary text-xs hover:bg-accent    -mt-1 transition duration-300 hover:brightness-75   rounded-r  inline-block"
            >
              <XMarkIcon className="h-6 hover:brightness-125 inline-block m-2" />
            </div>
          </div>
        </div>
        {isSelected && config.use_prompt_weights && (
          <div className="  w-full">
            <Slider
              className=""
              min={0}
              max={1}
              defaultValue={config.prompt_weights[selectedPrompt]}
              step={0.05}
              onAfterChange={(value) => {
                if (config.prompt_weights) {
                  if (uiConfig.alignWeights) {
                    setConfig({
                      ...config,
                      prompt_weights: rebalanceWeights(
                        config.prompt_weights,
                        i,
                        value
                      ),
                    });
                  } else {
                    setConfig({
                      ...config,
                      prompt_weights: config.prompt_weights.map((w, j) =>
                        j === i ? value : w
                      ),
                    });
                  }
                }
              }}
            />
            <div className="text-xs -mt-1 ml-2">
              {" "}
              weight: {config.prompt_weights[i]}{" "}
            </div>
          </div>
        )}
      </div>
    );
  });

  const promptInputRef = React.useRef<HTMLInputElement>(null);
  const uploadInputRef = React.useRef<HTMLInputElement>(null);
  const setPromptFromSample = (prompt: string) => {
    promptInputRef.current!.value = prompt;
    const holder = config.prompt;
    holder[selectedPrompt] = prompt;
    //  set config.prompt[selectedPrompt] to promptInputRef.current!.value
    setConfig({ ...config, prompt: holder });
  };

  const updateConfigWithPrompt = () => {
    if (promptInputRef.current && !loading) {
      const holder = config.prompt;
      holder[selectedPrompt] = promptInputRef.current.value;
      setConfig({ ...config, prompt: holder });
      promptInputRef.current.blur();
    }
  };

  return (
    <div className=" pb-4">
      {/* </div> */}

      <GeneratorControlsView
        setConfig={setConfig}
        config={config}
        generate={fetchGeneration}
        isLoading={loading}
      />
      <div className="relative  ">
        {loading && (
          <div className="h-full bg-primary rounded bg-opacity-20 bg-white cursor-progress  z-10 w-full  absolute top-0"></div>
        )}
        <div
          className={`${
            !loading ? "" : " "
          } border border-secondary rounded p-3`}
        >
          <div className="  flex relative  ">
            {config.init_image && (
              <div className="mr-3">
                <UploadView config={config} setConfig={setConfig} />
              </div>
            )}
            <div className="flex-1 ">
              {" "}
              <>
                <div
                  className={`${
                    loading ? " pointer-events-none" : ""
                  }     gap-2 md:flex itpems-start   `}
                >
                  <textarea
                    className={` w-full p-3 text-gray-700 flex-1 rounded border border-gray-400 inline-block  ${
                      loading ? "brightness-90 pointer-events-none" : ""
                    }`}
                    placeholder="Enter Prompt"
                    // type={"text"}
                    defaultValue={config.prompt}
                    // value={config.prompt}
                    style={{
                      height: "100px",
                    }}
                    ref={promptInputRef}
                    onKeyDown={(e) => {
                      if (
                        e.key === "Enter" &&
                        promptInputRef.current &&
                        !loading
                      ) {
                        updateConfigWithPrompt();
                      }
                    }}
                    onBlur={() => {
                      updateConfigWithPrompt();
                    }}
                  />

                  <div className="gap-2 flex flex-col mt-2 md:mt-0 ">
                    <Tooltip className="block " title="Upload an image.">
                      <label htmlFor="getFile1" className="file_upload">
                        <LaunchButton
                          onClick={() => {
                            uploadInputRef.current?.click();
                          }}
                        >
                          <PhotoIcon className="inline-block h-6" /> Upload
                          image
                        </LaunchButton>
                      </label>
                    </Tooltip>

                    <input
                      ref={uploadInputRef}
                      type="file"
                      accept="image/*"
                      id="getFile1"
                      className="hidden"
                      onChange={(event) => {
                        if (event.target.files && event.target.files[0]) {
                          getBase64(event.target.files[0]);
                          resetError();
                          uploadInputRef.current!.value = "";
                        } else {
                          setError({
                            status: false,
                            message: "No uploaded file selected",
                          });
                        }
                      }}
                    />

                    <span>
                      <LaunchButton
                        onClick={() => {
                          fetchGeneration(config);
                        }}
                      >
                        <div className="   ">
                          {loading && (
                            <div className="inline-block   ">
                              <Cog6ToothIcon className="relative -pb-2  animate-spin  inline-flex rounded-full h-5 w-5" />
                            </div>
                          )}
                          {!loading && (
                            <span className="inline-block ">
                              {" "}
                              <SparklesIcon className="inline-block h-5 mr-2 -mt-1" />
                              {/* <Icon icon="app" size={6} /> */}
                              Generate
                            </span>
                          )}
                        </div>{" "}
                      </LaunchButton>
                    </span>
                  </div>
                </div>
              </>
            </div>
          </div>

          {/* Show prompt controls  */}
          <div className="mt-4   text-primary    ">
            <div className="flex gap-2">
              <div className="flex flex-col">
                <SecondaryButton
                  onClick={() => {
                    if (config.prompt_weights) {
                      setConfig({
                        ...config,
                        prompt: [...config.prompt, ""],
                        prompt_weights: rebalanceWeights(
                          [...config.prompt_weights, 1],
                          selectedPrompt,
                          config.prompt_weights[selectedPrompt]
                        ),
                      });
                    }

                    setSelectedPrompt(config.prompt.length);
                  }}
                  className="py-3"
                >
                  <PlusIcon className="inline-block h-4 text-accent " /> Add
                  prompt
                </SecondaryButton>
                {config.use_prompt_weights && (
                  <div className="mt-1 py-2 text-primary flex gap-2 rounded ">
                    <Checkbox
                      className="text-primary"
                      checked={uiConfig.alignWeights}
                      onChange={() => {
                        if (!uiConfig.alignWeights) {
                          setConfig({
                            ...config,
                            prompt_weights: rebalanceWeights(
                              config.prompt_weights,
                              selectedPrompt,
                              config.prompt_weights[selectedPrompt]
                            ),
                          });
                        }

                        setUiConfig({
                          ...uiConfig,
                          alignWeights: !uiConfig.alignWeights,
                        });
                      }}
                    >
                      Align weights
                    </Checkbox>
                  </div>
                )}
              </div>
              <div className="gap-2 flex flex-wrap flex-1">{promptList}</div>
            </div>
          </div>
        </div>
      </div>

      <div className="hidden  relative   border border-secondary rounded p-2 mt-6   mb-2">
        <div className="tex-xs -mt-5  ">
          <div className="inline-block px-2 bg-primary mb-2">Settings</div>
        </div>

        <div className="right-4 absolute -top-3   px-2 bg-primary rounded">
          {" "}
          <Switch
            className="mr-2"
            checked={advancedOptions}
            onChange={() => {
              setAdvancedOptions(!advancedOptions);
            }}
          />
          Advanced{" "}
        </div>

        {advancedOptions && <div className="p-2"> advanced stuff</div>}
      </div>

      {/* <div className="h-6 w-6 relative"> */}

      <div className="flex gap-3 mt-4 mb-4">
        <div className={`flex-1 mt-3 ${loading ? "pointer-events-none" : ""}`}>
          <SamplesView setPrompt={setPromptFromSample} />
        </div>
      </div>

      {/* </div> */}
      {loading && (
        <>
          <div className="rounded bg-secondary mt-4 p-3">
            <span className="inline-block h-6 w-6 relative mr-2">
              <Cog8ToothIcon className="animate-ping text-accent absolute inline-flex h-full w-full rounded-ful  opacity-75" />
              <Cog8ToothIcon className="relative text-accent animate-spin  inline-flex rounded-full h-6 w-6" />
            </span>
            Working .... generations may take a while, depending on the capacity
            of your GPU.
          </div>
          <div className="relative">
            <div className="loadbar rounded-b"></div>
          </div>
        </>
      )}

      {error && !error.status && (
        <div className="mt-6">
          {" "}
          <div className="font-semibold  pb-1 mb-2 text-red-400">
            <ExclamationCircleIcon className="inline-block h-6 w-6 -mt-1" /> An
            error occurred. Please try again later.
          </div>
          <div className="text-secondpary border-red-400 border rounded bg-secondary p-2 px-3">
            {" "}
            {error.message}.
          </div>
        </div>
      )}

      {results && (
        <div className="  mt-6 rounded ">
          <GalleryView
            data={results}
            config={config}
            setInitImage={updateInitImage}
          />
        </div>
      )}
    </div>
  );
};
export default GenerateView;
